import React, { Component } from "react";
import moment from "moment";
import { saveAs } from 'file-saver';
import ReactHtmlParser from 'react-html-parser';
import { getCertificate } from '../../../../../../Services/Actions/ugcRightsActions'

const timeZoneImageReplace = (imageReplace) => {
    try {
        var repalceData = imageReplace
        if (imageReplace && String(imageReplace).length > 0 && String(imageReplace).includes(`"img/flags/`)) {
            repalceData = String(imageReplace).replace(`"img/flags/`, `"https://cloud.taggbox.com/ugc-suite/media/images/flags/`)
            if (repalceData && String(repalceData).includes(".png")) {
                repalceData = String(repalceData).replace(`.png`, `.svg`)
            }
            repalceData = String(repalceData).replace(`<img`, `<img style="width: 20px;height:20px;" `)
            return repalceData
        }
        return imageReplace
    }
    catch {
        return imageReplace
    }
}

const userAgentImageReplace = (imageReplace) => {
    try {
        var repalceData = imageReplace
        if (imageReplace && String(imageReplace).length > 0 && String(imageReplace).includes(`"img/browser-svg/`)) {
            repalceData = String(imageReplace).replace(`"img/browser-svg/`, `"https://cloud.taggbox.com/ugc-suite/media/icons/browser-svg/`)
            repalceData = String(repalceData).replace(`<img`, `<img style="width: 20px;height:20px;" `)
            return repalceData
        }
        return imageReplace
    }
    catch {
        return imageReplace
    }
}

